.skill-calculations-container {
	color: white;
	margin: 1em;
	margin-left: 2em;
	padding: 0.7em;
	min-width: 180px;
	width: 12%;
	margin: 1.2em 1em;
	background: var(--color-basic-calc);
	box-shadow: var(--shadow-basic);
	position: relative;
}

.empty-skill-calc {
	border: 2px solid #869B82;
	background: #1a1918e0;
	height: 100%;
	width: 100%;
	top: 0;
	left: 0;
	position: absolute;
	z-index: 2;
	display: flex;
	justify-content: center;
	align-items: center;
}

.skill-placeholder-img {
	width: 120px;
	color: white;
	-webkit-user-select: none;       
	-moz-user-select: none; 
	-ms-user-select: none; 
	user-select: none; 
}

.calc-skill-icon {
	width: 50px;
	height: 50px;
	background-size: contain;
	margin: 0 auto;
}

.skill-information-container {
	display: flex;
	justify-content: space-between;
}

.calculation-description, .calculation-results {
	width: 48%;
	font-size: 0.8em;
	padding: 0.1em;
	margin-top: 0.5em;
}

.calculation-description {
	text-align: right;
}

.calculation-results {
	text-align: left;
}

.skill-notes-heading {
	margin-bottom: 0.6em;
	color: #faac1c;
	text-transform: uppercase;
}

.skill-notes {
	text-align: center;
	margin: 1em 0 0.6em 0;
	text-transform: lowercase;
}

.skill-cooldown {
	margin-bottom: 0.6em;
	background-color: #161D1D;
	width: 100%;
	height: 50px;
	text-align: center;
	display: flex;
	flex-direction: column;
	justify-content: center;
	padding: 1.8em;
}

.skill-cooldown-header {
	text-align: center;
	font-size: 0.8em;
	padding-top: 0.3em;
	color: #FAAC1C;
}

.skill-cooldown-text {
	font-size: 1.5em;
}

.skill-has-buff {
	cursor: pointer;
}

.skill-has-inactive-buff {
	background: #39392e;
}

.skill-has-active-buff {
	background: #3b640c;
}

.skill-modifier {
	width: 0;
	height: 0;
	top: 0;
	right: 0;
	position: absolute;
	cursor: pointer;
}

.skill-modifier:before {
    position: absolute;
    top: -1px;
    right: -1px;
    width: 0;
    height: 0;
    border: 20px solid #b3b3b3;
    border-left-color: transparent;
    border-bottom-color: transparent;
    content: "";
}

.skill-modifier:after {
    display: block;
    position: absolute;
    top: 0px;
    right: 4px;
    content: "✎";
    color: black;
    font-size: 15px;
}
